


















































import helper from '../../common/helper.js';
var _self;
export default {

	data() {
		self = this
		return {
			mobile: '',
			tipsCode: "获取验证码",
			code: '',
			codestr: '',
			timeNum: 60,
			clikType: false,
			timeOut: 60,
		};
	},
	methods: {
		// 获取验证码
		openCodeDialog: function() {
			var that = this
			if (that.mobile == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入你的手机号码'
				});
				return;
			} else if (!helper.checkmobile(that.mobile)) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的手机号码'
				});
				return;
			} else if (!that.clikType) {
				that.codestr = helper.MathRand(4);
				that.clikType = true;
				uni.request({
					url: 'http://utf8.api.smschinese.cn/',
					method: 'GET',
					data: {
						Uid: "xingjiushenqi",
						Key: "d41d8cd98f00b204e980",
						smsMob: that.mobile,
						smsText: "您的验证码为：" + that.codestr + "，有效期5分钟"
					},
					success: res => {
						if (res.data == "1") {
							that.getTime()
						} else {
							uni.showToast({
								icon: 'none',
								title: '获取验证码失败,请重试'
							});
						}
					},
					fail: () => {
						uni.hideLoading();
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试'
						});
					}
				});
			}
		},
		// 验证码倒计时
		getTime() {
			var that = this
			var timer = setInterval(function() {
				that.timeNum--;
				if (that.timeNum <= 0) {
					that.tipsCode = "重新获取验证码";
					that.timeNum = that.timeOut;
					that.clikType = false;
					clearInterval(timer);
				} else {
					that.tipsCode = "重新发送" + that.timeNum + "秒";
				}
			}, 1000)
		},
		formSubmit: function(e) {
			if (this.mobile == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入你的手机号码'
				});
				return;
			}
			if (!helper.checkmobile(this.mobile)) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的手机号码'
				});
				return;
			}
			if (this.code == "" || this.code != this.codestr || this.codestr !=
				this.code) {
				uni.showToast({
					icon: 'none',
					title: '验证码错误'
				});
				return;
			}
			uni.showLoading({
				mask: true,
				title: '登录中，请稍候'
			});
			uni.request({
				url: helper.websiteUrl + '/user/loginbymobile',
				data: {
					loginMark: helper.getloginMark(),
					token: '',
					data: '{"mobile": "' + self.mobile + '","type":"1"}'
				},
				method: 'GET',
				dataType: 'json',
				success: (res) => {
					uni.hideLoading();
					if (res.data.code == 200) {
						//console.log(JSON.stringify(res))
						var userinfo = res.data.data.baseinfo;
						uni.setStorageSync("token", userinfo.token);
						helper.setstate(userinfo.userId, userinfo.mobile, userinfo.nickName);
						uni.showToast({
							title: '登录成功'
						});
						uni.switchTab({
							url: '../index/index'
						});
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				}
			});
		},
		getuserinfo() { // 微信快捷登录
			uni.showLoading({
				mask: true,
				title: '登陆中，请稍候'
			});
			//获取登录code
			uni.login({
				provider: 'weixin',
				success: function(res) {						
					uni.hideLoading();
					var objz = {};
					if (res.code) {
						uni.showLoading({
							mask: true,
							title: '登录中，请稍候'
						});
						//获取微信会员信息
						uni.getUserInfo({
							success: function(resuserinfo) {
								//获取openid
								uni.request({
									url: 'https://api.weixin.qq.com/sns/jscode2session?appid=' + self.appid + '&secret=' + self.secret +
										'&js_code=' +
										res.code + '&grant_type=authorization_code',
									data: {},
									method: 'GET',
									dataType: 'json',
									success: function(ress) {
										//console.log(JSON.stringify(ress));
										var openid = ress.data.openid;
										//注册和登录
										uni.request({
											url: helper.websiteUrl + 'user/reg',
											data: {
												loginMark: helper.getloginMark(),
												token: '',
												data: '{"openid": "' + openid + '","headimgurl":"' + resuserinfo.userInfo.avatarUrl + '","nikename":"' +
													resuserinfo.userInfo.nickName +
													'","province":"' + resuserinfo.userInfo.province + '","country":"' + resuserinfo.userInfo.country +
													'","city":"' + resuserinfo.userInfo.city +
													'","sex":"' + resuserinfo.userInfo.gender + '"}'
											},
											method: 'GET',
											dataType: 'json',
											success: (data) => {
												uni.hideLoading();
												if (data.data.code == 200) {
													var userinfo = data.data.data.baseinfo;
													uni.setStorageSync("token", userinfo.token);
													helper.setstate(userinfo.userId, userinfo.mobile, userinfo.nickName);
													uni.showToast({
														title: '登录成功'
													});
													uni.switchTab({
														url: '../index/index'
													});
												} else {
													uni.showToast({
														icon:'none',
														title: data.data.info
													});
												}
											},
											fail: () => {
												uni.hideLoading();
												uni.showToast({
													icon: 'none',
													title: '网络异常,请稍后重试'
												});
											}
										});
		
									},
		
								});
							},
							fail: function() {
								uni.hideLoading();
							}
						});
					} else {
						uni.showToast({
							icon: 'none',
							title: '获取用户登录态失败！' + res.errMsg
						});
					}
				},
				fail: function() {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络错误，请稍后再试'
					})
				}
			});
		
		},
		openregister() { //打开注册页面
			uni.navigateTo({
				url: 'register',
			});
		},
		openlogin() { //打开登录页面 
			uni.navigateTo({
				url: 'sign',
			});
		}
	}
}
